export default {
  data: () => ({
    distrs: []
  }),
  props: {
    districts:{
      type: Array
    }
  },
  name: "DistrictList",
  created() {
    this.districts.forEach(dis => {
      this.distrs.push(dis.name['name'])
    })
    // console.log(this.distrs)
  }
}
